.posts-form{
    background-color: #e4f4fa;
    display: flex;
    flex-direction: column;
    text-align: center ;
    padding: 1.5rem;
    margin: 2rem auto;
    border-radius: 15px;
}

.form{
    margin: auto;
    width: 50%;
    display: flex;
    flex-direction: column;
}

input, textarea{
    border: none;
    margin-bottom:1rem;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 15px;
    text-align: center;
    padding: 0.5rem;
}